import React, { useEffect, useState } from "react";
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import IconButton from "@mui/material/IconButton";
import DeleteIcon from "@mui/icons-material/Delete";
import Tooltip from "@mui/material/Tooltip";
import { DataGrid } from "@mui/x-data-grid";
import "./App.css";
import AddBook from "./AddBook";

function App() {
  const [books, setBooks] = useState([]);
  const columns = [
    { field: "title", headerName: "Title", width: 150 },
    { field: "author", headerName: "Author", width: 150 },
    { field: "year", headerName: "Year", width: 150 },
    { field: "isbn", headerName: "ISBN", width: 150 },
    { field: "price", headerName: "Price", width: 150 },
    {
      field: "delete",
      headerName: "delete",
      width: 150,
      renderCell: (e) => {
        return (
          <Tooltip title="Delete todo">
            <IconButton
              size="small"
              color="error"
              onClick={() => {
                // console.log(e);
                deleteTodo(e);
              }}
            >
              <DeleteIcon />
            </IconButton>
          </Tooltip>
        );
      },
    },
  ];

  useEffect(() => {
    fetchItems();
  });
  const fetchItems = () => {
    fetch(
      "https://bookstore-9285f-default-rtdb.europe-west1.firebasedatabase.app/books/.json"
    )
      .then(
        (response) => response.json()
        // response.set("Access-Control-Allow-Origin", "*");
      )
      .then((data) => addKeys(data))
      .catch((err) => console.error(err));
  };
  // Add keys into the todo objects
  const addKeys = (data) => {
    const keys = Object.keys(data);
    const valueKeys = Object.values(data).map((item, index) =>
      Object.defineProperty(item, "id", { value: keys[index] })
    );
    setBooks(valueKeys);
  };
  const addBook = (newTodo) => {
    console.log(newTodo);
    fetch(
      "https://bookstore-9285f-default-rtdb.europe-west1.firebasedatabase.app/books/.json",
      {
        method: "POST",
        body: JSON.stringify(newTodo),
      }
    )
      .then((response) => fetchItems())
      .catch((err) => console.error(err));
  };
  const deleteTodo = (ID) => {
    const deleteBook = ID.row.id;
    console.log(deleteBook);
    console.log("should delte this now");
    fetch(
      `https://bookstore-9285f-default-rtdb.europe-west1.firebasedatabase.app/books/${deleteBook}.json`,
      {
        method: "DELETE",
      }
    )
      .then((response) => fetchItems())
      .catch((err) => console.error(err));
  };
  return (
    <div className="App">
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h5">Bookstore</Typography>
        </Toolbar>
      </AppBar>
      <AddBook addBook={addBook}></AddBook>

      <div style={{ height: 400, width: "100%" }}>
        <DataGrid rows={books} columns={columns} />
      </div>
    </div>
  );
}

export default App;
